
/*--------------------------------------------------------------
5.0 Typography
--------------------------------------------------------------*/
body, button, input, select, textarea {
  color: #333;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-size: .9375rem;
  font-weight: 400;
  line-height: 2.16;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  clear: both;
  line-height: 1.5;
  margin: 0 0 .75em;
  padding: 1.5em 0 0;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  font-weight: 400;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child,
.h1:first-child,
.h2:first-child,
.h3:first-child,
.h4:first-child,
.h5:first-child,
.h6:first-child{
  padding-top: 0;
}

h1,
.h1 {
  font-size: 24px;
  font-size: 1.5rem;
}

h2,
.h2 {

  font-size: 20px;
  font-size: 1.25rem;
}

h3,
.h3 {

  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 400;
}

h4,
.h4 {

  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
}

h5,
.h5 {

  font-size: 13px;
  font-size: .8125rem;
  font-weight: 400;
  letter-spacing: .15em;
  text-transform: uppercase;
}

h6,
.h6 {
  color: #333;
  font-size: 15px;
  font-size: .9375rem;
  font-weight: 800;
}

p {
  margin: 0 0 1.5em;
  padding: 0;
}

dfn, cite, em, i {
  font-style: italic;
}

blockquote {
  color: #666;
  font-size: 18px;
  font-size: 1.125rem;
  font-style: italic;
  line-height: 1.7;
  margin: 0;
  overflow: hidden;
  padding: 0;
}

blockquote cite {
  display: block;
  font-style: normal;
  font-weight: 600;
  margin-top: .5em;
}

address {
  margin: 0 0 1.5em;
}

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 15px;
  font-size: .9375rem;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 15px;
  font-size: .9375rem;
}

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark, ins {
  background: #eee;
  text-decoration: none;
}

big {
  font-size: 125%;
}

blockquote {
  quotes: "" "";
}

q {
  quotes: "" "" "" "";
}

blockquote:before, blockquote:after {
  content: "";
}

:focus {
  outline: none;
}

